@charset "UTF-8";
.inBlock {
  display: inline-block;
}

.block {
  display: block;
}

.list--inline {
  display: flex;
}
.list--inline li {
  list-style-type: none;
}
.list--separated li:not(:first-child) {
  margin-left: 10px;
}
.list--separated li:not(:first-child)::before {
  content: "\B7";
  margin-right: 10px;
}
.list--spaced li:not(:first-child) {
  margin-left: 20px;
}
.list--ordered {
  list-style-type: none;
  counter-reset: listcounter;
}
.list--ordered li {
  position: relative;
  padding-left: 32px;
  line-height: 1.4;
}
.list--ordered li:not(:last-child) {
  margin-bottom: 10px;
}
.list--ordered li::before {
  counter-increment: listcounter; /* Increment the value of listcounter counter by 1 */
  content: counter(listcounter);
  color: #fff;
  margin-right: 4px;
  position: absolute;
  left: 0;
  font-size: 13px;
  background-color: #19a974;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
}
.list--loading li::before {
  counter-increment: listcounter; /* Increment the value of listcounter counter by 1 */
  content: counter(listcounter);
  color: #fff;
  margin-right: 4px;
  position: absolute;
  left: 0;
  font-size: 13px;
  background-color: #e0e0e0;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
}

.flex {
  display: flex;
}

.flex-inline {
  display: inline-flex;
}

/*
 * Remove pseudo elements created by clearfix as precaution
 */
.flex::before,
.flex::after,
.flex-inline::before,
.flex-inline::after {
  display: none;
}

/*
 * Align items along the main axis of the current line of the flex container
 * Row: Horizontal
 */
.flex-left {
  justify-content: flex-start;
}

.flex-center {
  justify-content: center;
}

.flex-right {
  justify-content: flex-end;
}

.flex-between {
  justify-content: space-between;
}

.flex-around {
  justify-content: space-around;
}

/*
 * Align items in the cross axis of the current line of the flex container
 * Row: Vertical
 */
.flex-stretch {
  align-items: stretch;
}

.flex-top {
  align-items: flex-start;
}

.flex-middle {
  align-items: center;
}

.flex-bottom {
  align-items: flex-end;
}

/*
 * Direction
 */
.flex-row {
  flex-direction: row;
}

.flex-row-reverse {
  flex-direction: row-reverse;
}

.flex-column {
  flex-direction: column;
}

.flex-column-reverse {
  flex-direction: column-reverse;
}

/**
  * Wrap
  */
.flex-nowrap {
  flex-wrap: nowrap;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-wrap-reverse {
  flex-wrap: wrap-reverse;
}

/*
 * Aligns items within the flex container when there is extra space in the cross-axis
 * Only works if there is more than one line of flex items
 */
.flex-wrap-stretch {
  align-content: stretch;
}

.flex-wrap-top {
  align-content: flex-start;
}

.flex-wrap-middle {
  align-content: center;
}

.flex-wrap-bottom {
  align-content: flex-end;
}

.flex-wrap-between {
  align-content: space-between;
}

.flex-wrap-around {
  align-content: space-around;
}

/*
 * Item Ordering
 */
.flex-first {
  order: -1;
}

.flex-last {
  order: 99;
}

/*
 * No Flex: 0 0 auto
 * Content dimensions
 */
.flex-none {
  flex: none;
}

/*
 * Relative Flex: 1 1 auto
 * Space is allocated considering content
 */
.flex-auto {
  flex: auto;
}

/*
 * Absolute Flex: 1 1 0%
 * Space is allocated solely based on flex
 */
.flex-1 {
  flex: 1 1;
}

.flex-15 {
  flex: 1.5 1;
}

.flex-75 {
  flex: 0.75 1;
}

.flex-basis-30p {
  flex-basis: 30%;
}

.flex-basis-20p {
  flex-basis: 20%;
}

.flex-basis-17p {
  flex-basis: 17%;
}

.flex-self-center {
  align-self: center;
}

.flex-self-start {
  align-self: flex-start;
}

.flex-self-end {
  align-self: flex-end;
}

